.share {
	width: 100%;
	height: 170px;
	box-shadow: 18px 19px 20px -17px #7996d7;
	border-radius: 15px;
}

.shareWrapper {
	padding: 10px;
}

.shareTop {
	display: flex;
	align-items: center;
}

.shareProfileImg {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	object-fit: cover;
	margin-right: 10px;
}

.shareInput {
	border: none;
	width: 100%;
}

.shareInput:focus {
	outline: none;
}

.shareHr {
	margin: 20px;
}

.shareButtons {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.shareOptions {
	display: flex;
	margin-left: 20px;
}

.shareOption {
	display: flex;
	align-items: center;
	margin-right: 15px;
	cursor: pointer;
}

.shareIcon {
	margin-right: 3px;
}

.shareOptionText {
	font-size: 14px;
	font-size: 550;
}

.shareButton {
	border: none;
	padding: 6px 17px;
	background-color: rgb(49, 158, 192);
	border-radius: 5px;
	color: white;
	cursor: pointer;
	margin-right: 20px;
	font-weight: 500;
}