.profile {
	display: flex;
}

.profileRight {
	flex: 10.5;
}

.profileCover {
	height: 320px;
	position: relative;
}

.profileCoverImg {
	width: 100%;
	height: 250px;
	object-fit: cover;
}

.profileUserImg {
	position: absolute;
	left: 0;
	right: 0;
	width: 100px;
	height: 100px;
	border-radius: 50%;
	margin: auto;
	top: 150px;
	border: 3px solid white;
}

.profileInfo {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.profileInfoName {
	font-size: 24px
}

.profileRightBottom {
	display: flex;
}