.post {
	width: 100%;
	box-shadow: 18px 19px 20px -17px #7996d7;
	border-radius: 15px;
	margin: 30px 0;
}

.postWrapper {
	padding: 10px;
}

.postProfileImg {
	width: 32px;
	height: 32px;
	border-radius: 50%;
	object-fit: cover;
}

.postTop {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.postTopLeft {
	display: flex;
	align-items: center;
}

.postUsername {
	font-size: 15px;
	font-weight: 550;
	margin: 0 10px;
}

.postDate {
	font-size: 12px;
}

.postCenter {
	margin: 20px 0;
}

.postImg {
	margin-top: 20px;
	width: 100%;
	max-height: 500px;
	object-fit: contain;
}

.postBottom {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.postBottomLeft {
	display: flex;
	align-items: center;
}

.likeIcon {
	width: 24px;
	height: 24px;
	margin-right: 4px;
	cursor: pointer;
}

.postLikeCounter {
	font-size: 15px;
}

.postCommentText {
	cursor: pointer;
	border-bottom: 1px solid rgb(192, 188, 188);
	font-size: 15px;
	margin-right: 3px;
}